import React from 'react';
import './styles.css';

class Sort extends React.Component{
  render(){
    const sort = ['Rating: Low','Rating: High','Popularity: Low','Popularity: High'];
     const categories = sort.map((items,index) => {
      return (
        <li key={'sort' + index} className="items">{items}</li>
      )
    });
    return(
          <div className="sort">
            <div className="container">
              <div className="sort_text">Sort By</div>
              <div className="sort_icon"><i className="icon ion-chevron-down"></i></div>
              <div className="clearfix"></div>
            </div>
            <ul className="list">{categories}</ul>
          </div>
      )
  }
}

export default Sort


