import React from 'react';
import './styles.css';

class Year extends React.Component{
  render(){
    const years = ['2010','2011','2012','2013','2014','2015','2016','2017','2018'];
     const categories = years.map((items,index) => {
      return (
        <li key={'years' + index} className="items">{items}</li>
      )
    });
    return(
          <div className="year">
            <div className="container">
              <div className="year_text">Year</div>
              <div className="year_icon"><i className="icon ion-chevron-down"></i></div>
              <div className="clearfix"></div>
            </div>
            <ul className="list">{categories}</ul>
          </div>
      )
  }
}

export default Year


