import React from 'react';
import MovieCardBox from './MovieCardBox';
import './styles.css';

class MovieCard extends React.Component{
  constructor(){
    super();
    this.state = {
      data : [],
    }
  }
  componentDidMount(){
    fetch('https://api.themoviedb.org/3/movie/popular?api_key=80a5e68d306aa343acd190456d8d687f&language=en-US&page=1').then((Response) => Response.json()).then((findresponse)=>{
      console.log(findresponse.results);
      this.setState({
        data:findresponse.results
      })
    })
  }
  date_format(new_date){
    var m_names = ["Jan", "Feb", "Mar", 
    "Apr", "May", "Jun", "Jul", "Aug", "Sep", 
    "Oct", "Nov", "Dec"];

    var d = new Date(new_date);
    var curr_date = d.getDate();
    var curr_month = d.getMonth();
    var curr_year = d.getFullYear();
    return (curr_date + " " + m_names[curr_month] 
    + " " + curr_year);
    }

  render(){
    return(
  <div className="movie-card-container">
    {
      this.state.data.map((dynamicData,key) =>
          <MovieCardBox key={'movie_' + key} date={this.date_format(dynamicData.release_date)} image={dynamicData.poster_path} title={dynamicData.title} votes={dynamicData.vote_average} language={dynamicData.original_language}  />
        )
      
    }
    

  </div>

      )
  }
}

export default MovieCard


